@import url("https://fonts.googleapis.com/css?family=Lora|Montserrat:800");

.Header-link-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: baseline;
      align-items: baseline;
}

.Header-link {
  font-family: "Montserrat", sans-serif;
  padding: 17px;
  font-size: 15px;
  color: #8bb1b6;
  /* color: rgb(255, 103, 103); */
  /* color: #A1CEC7; */
  text-decoration: none;
}

.Header-link:hover {
  -webkit-text-stroke: #8bb1b6 1px;
}

.NavLink {
  font-family: "Montserrat", sans-serif;
  padding: 26px 40px;
  font-size: 13px;
  color: #a1cec7;
  text-decoration: none;
}



