.About {
  display: -ms-flexbox;
  display: flex;
  padding: 80px 0 0 0;
  margin-bottom: 80px;
}

.about-text {
  width: 60%;
  margin-left: 7%;
}

.Headshot {
  display: inline-block;
  margin-left: 15%;
  margin-top: 20px;
  width: 25%;
  height: 0%;
  -webkit-animation-name: fade-in-from-left;
          animation-name: fade-in-from-left;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  /* border-radius: 15%; */
  /* border: 3px white solid;
    box-shadow: 0 4px 8px 0 rgba(165, 109, 109, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
}

@-webkit-keyframes fade-in-from-left {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-40px);
            transform: translateX(-40px);
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-in-from-left {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-40px);
            transform: translateX(-40px);
  }
  100% {
    opacity: 1;
  }
}

.About-title {
  margin-top: 25px;
  font-family: brandon-grotesque, sans-serif;
  font-size: 30px;
  /* animation-name: fade-in-from-top;
  animation-duration: 1s; */
  /* color: #f49e85; */
}

.Personal-intro {
  display: inline-block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
  width: 63%;
  font-family: "Montserrat", sans-serif;
  font-size: 15px;
  line-height: 24px;
  color: black;
  -webkit-animation-name: fade-in-from-right;
          animation-name: fade-in-from-right;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

@-webkit-keyframes fade-in-from-right {
  0% {
    opacity: 0;
    -webkit-transform: translateX(40px);
            transform: translateX(40px);
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-in-from-right {
  0% {
    opacity: 0;
    -webkit-transform: translateX(40px);
            transform: translateX(40px);
  }
  100% {
    opacity: 1;
  }
}

.text {
  display: inline;
  color: #ff5448;
  padding: 0;
}

.lessbold-text {
  color: rgb(0, 0, 0);
}

.lessbold-text:hover {
  color: rgb(148, 148, 148);
}



